import { createGlobalStyle} from "styled-components"
export const GlobalStyles = createGlobalStyle`
  body {
    background: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
  };

  .card {
    background: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
    border-color: #646464;
  };

  .list-group-item {
    background: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
    border-color: #646464;
  };

  .form-control {
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
  };

  .modal-content {
    background-color: ${({ theme }) => theme.body};
    border-color: #646464;
  };
  .modal-header {
    background-color: ${({ theme }) => theme.body};
    border-color: #646464;
  };
  .modal-body {
    background-color: ${({ theme }) => theme.body};
    border-color: #646464;
  };
  .modal-footer {
    background-color: ${({ theme }) => theme.body};
    border-color: #646464;
  };
  `